<template>
  <div id="queryui">
    <section>
        <div>
          <el-row>
            <el-col :span="24">
              <div class="search-panel">
                <el-row :gutter="4">
                    <el-col :span="3">
                      <div class="multi-select">
                        <el-select v-model="multiSelect" multiple filterable :filter-method="filterMethod" placeholder="" style="width:100%">
                            <el-option
                              v-for="item in options"
                              :key="item.index"
                              :label="item.index"
                              :value="item.index">
                            </el-option>
                        </el-select>
                      </div>
                    </el-col>
                    <el-col :span="17">
                      <div class="search-box">
                        <el-input
                          placeholder=""
                          icon="star-off"
                          v-model="searchBoxInput"
                          :on-icon-click="handleIconClick">
                        </el-input>
                      </div>
                    </el-col>
                    <el-col :span="4">
                      <div class="search-button">
                        <el-button-group>
                          <el-button type="primary" icon="search" v-on:click="searchFunc">Search</el-button>
                          <el-button type="primary"><i class="fa fa-download"></i> </el-button>
                          <el-button type="primary"><i class="fa fa-history"></i> History</el-button>
                        </el-button-group>
                      </div>
                    </el-col>
                </el-row>
              </div>
            </el-col>
            <el-col :span="24">
              <div class="search-wrapper">
                <div class="bar-chart"><div class="bullet-point"></div> {{searchTotal}} .  {{searchTime}} ms.</div>
              </div>
            </el-col>
            <el-col :span="24">
                <div class="search-body">
                   <el-row :gutter="4">
                    <el-col :span="3">
                      <div class="filter-wrapper">
                        <h2>Sort By</h2>
                        <div class="field-set-wrapper">
                            <el-select v-model="sortFields" filterable placeholder="" style="width:100%">
                              <el-option
                                v-for="item in fields"
                                :key="item"
                                :label="item"
                                :value="item">
                              </el-option>
                              <el-option label="_score" value="_score">
                              </el-option>
                              <el-option label="_type" value="_type">
                              </el-option>
                              <el-option label="_index" value="_index">
                              </el-option>
                              <el-option label="_uid" value="_uid">
                              </el-option>
                            </el-select>
                        </div>
                      </div>
                       <div class="filter-wrapper">
                        <h2>Display</h2>
                        <div class="field-set-wrapper">
                            <el-select v-model="total" filterable placeholder="" style="width:100%">
                              <el-option label="10" value="10">
                              </el-option>
                              <el-option label="50" value="50">
                              </el-option>
                              <el-option label="100" value="100">
                              </el-option>
                              <el-option label="200" value="200">
                              </el-option>
                               <el-option label="500" value="500">
                              </el-option>
                               <el-option :label="':'+searchTotal" :value="searchTotal">
                              </el-option>
                            </el-select>
                        </div>
                      </div>
                      <div class="filter-wrapper">
                        <h2></h2>
                        <div class="field-set-wrapper">
                          <h3>
                             <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
                          </h3>
                          <ul>
                            <el-checkbox-group v-model="checkedFields" size="small" @change="handleCheckedCitiesChange">
                              <li v-for="field in fields" :key="field">
                                 <el-checkbox :label="field"  >{{field}}</el-checkbox>
                              </li>
                            </el-checkbox-group>
                          </ul>
                        </div>
                      </div>
                    </el-col>
                    <el-col :span="21">
                      <el-table :data="tableData" border stripe resizable style="width: 100%" :fit="true">
                        <el-table-column type="expand" fixed> 
                          <template scope="props">
                            <el-form label-position="left" class="demo-table-expand">
                              <el-form-item label="Index"><span>{{ props.row._index }} </span></el-form-item>
                              <el-form-item label="Type" ><span>{{ props.row._type }} </span></el-form-item>
                              <el-form-item label="ID" ><span>{{ props.row._id }} </span></el-form-item>
                              <el-form-item :label="key" v-for="(fd, key) in props.row._source" :key="key"><span> {{ fd }} </span></el-form-item>
                            </el-form>
                          </template>
                        </el-table-column>
                        <div v-if="checkedFields.length > 0">
                           <el-table-column label="Index" prop="_index" width="210" show-overflow-tooltip> </el-table-column>
                          <el-table-column label="Type" prop="_type" width="210" show-overflow-tooltip> </el-table-column>
                          <el-table-column label="ID" prop="_id" width="210" show-overflow-tooltip> </el-table-column>
                          <el-table-column :label="field" show-overflow-tooltip :prop="'_source.'+field" v-for="field in checkedFields" :key="field"  width="200" style="white-space:nowrap;overflow:hidden;text-overflow:ellipsis;"> </el-table-column>
                        </div>
                        <div v-else>
                           <el-table-column label="Index" prop="_index" width="210" show-overflow-tooltip> </el-table-column>
                           <el-table-column label="Type" prop="_type" width="210" show-overflow-tooltip> </el-table-column>
                           <el-table-column label="ID" prop="_id" width="210" show-overflow-tooltip> </el-table-column>
                           <el-table-column show-overflow-tooltip label="Source" prop="_source" :formatter="jsondecode" style="white-space:nowrap;overflow:hidden;text-overflow:ellipsis;"> </el-table-column>
                        </div>
                      </el-table>
                    </el-col>
                </el-row>
                </div>
            </el-col>
          </el-row>
        </div>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'queryui',
  data: function () {
    return {
      total: '10',
      sortFields: '_score',
      options: [],
      multiSelect: [],
      searchBoxInput: '*',
      searchTotal: 0,
      searchTime: 0,
      checkAll: true,
      checkedFields: [],
      isIndeterminate: true,
      tableData: []
    }
  },
  computed: {
    ...mapGetters([
      'activeIndex',
      'indexList',
      'serverhost',
      'result',
      'fields'
    ])
  },
  watch: {
    checkedFields () {
    },
    result () {
      this.tableData = this.result.hits.hits
      this.searchTotal = this.result.hits.total
      this.searchTime = this.result.took
    },
    indexList () {
      this.options = this.indexList.slice(0, 50)
    }
  },
  methods: {
    jsondecode (row, column) {
      var source = row[column.property]
      if (source === undefined) {
        return ''
      }
      if (source.constructor === String) {
        return source
      }
      return JSON.stringify(source)
    },
    trim (str) {
      return str.replace(/^(\s|\xA0)+|(\s|\xA0)+$/g, '')
    },
    searchFunc () {
      var dsl = ``
      if (this.trim(this.searchBoxInput).length > 0) {
        var sort = {}
        sort[this.sortFields] = 'desc'
        dsl = {
          size: this.total,
          sort: [
            sort
          ],
          query: {
            query_string: {
              query: this.searchBoxInput
            }
          }
        }
        dsl = JSON.stringify(dsl)
        // dsl = `{ "size":"` + this.total + `","sort" : [{"` + this.sortFields + `": "desc"}], "query" : { "query_string" : {"query" : "` + this.searchBoxInput + `"} }}`
      } else {
        sort = {}
        sort[this.sortFields] = 'desc'
        dsl = {
          size: this.total,
          sort: [
            sort
          ],
          query: {
            query_string: {
              query: '*'
            }
          }
        }
        dsl = JSON.stringify(dsl)
        // dsl = `{ "size":"` + this.total + `","sort" : [{"` + this.sortFields + `": "desc"}], "query" : { "query_string" : {"query" : "*"} }}`
      }
      console.log(dsl)
      var tmpVal = {url: '/_search', date: this.getTime(), host: this.serverhost, method: 'POST', indices: this.multiSelect.join(','), types: '', options: '_search', body: dsl}
      this.$store.dispatch('CURLSerachResult', tmpVal)
      var tmpVal2 = {url: '/_indices/mapping', date: this.getTime(), host: this.serverhost, method: 'POST', indices: this.multiSelect.join(','), types: '', options: '_search', body: dsl}
      this.$store.dispatch('CURLSerachFields', tmpVal2)
      // console.log(this.multiSelect, dsl)
    },
    filterMethod (query) {
      this.options = this.filterItems(query).slice(0, 50)
    },
    filterItems (query) {
      var arr = this.indexList.filter(function (el) {
        return el['index'].toLowerCase().indexOf(query.toLowerCase()) > -1
      })
      return arr
    },
    handleIconClick () {
    },
    handleCheckAllChange (event) {
      this.checkedFields = event.target.checked ? this.fields : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange (value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.fields.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.fields.length
    },
    getTime: function () {
      var date = new Date()
      var seperator1 = '-'
      var seperator2 = ':'
      var month = date.getMonth() + 1
      var strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    }
  },
  created: function () {
    this.options = this.indexList.slice(0, 50)
  }
}
</script>

<style>
.el-table .cell {
    white-space: normal;
    /*word-break: break-all;*/
    line-height: 24px;
    overflow:hidden !important;
    white-space:nowrap !important;
    text-overflow:ellipsis !important;
}
.bar-chart {
    font-size: 12px;
    margin-left: 2px;
}
.bullet-point {
    position: relative;
    top: -2px;
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: #42a9f4;
    border-radius: 100%;
    margin-right: 5px;
    vertical-align: middle;
}

.search-wrapper {
    height: 35px;
    line-height: 35px;
    padding: 0 5px;
    margin: 5px 0 5px 0;
    background-image: linear-gradient(#DFE7EC,#f1f1f1);
    border: 1px solid #ddd;
    border-radius: 2px;
    vertical-align: middle;
    display: flex;
}
.filter-wrapper {
    float: left;
    width: 100%;
    height: 100%;
    /*margin: 0 5px;*/
    font-size: 12px;
    overflow-y: auto;
    border: 1px solid #ddd;
    position: relative;
    border-radius: 2px;
    background: white;
}
.filter-wrapper h2 {
    color: #333;
    font-size: 14px;
    font-weight: 700;
    margin: 0;
    padding: 0 1em;
    line-height: 39px;
    height: 40px;
    border-bottom: 1px solid #ddd;
    background-image: linear-gradient(#DFE7EC,#f1f1f1);
}
h2 {
    display: block;
    font-size: 1.5em;
    -webkit-margin-before: 0.83em;
    -webkit-margin-after: 0.83em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    font-weight: bold;
}

.filter-wrapper .field-set-wrapper h3 {
    font-size: 12px;
    margin: .5em;
    position: relative;
}
.search-body .thead {
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
}

.filter-wrapper ul {
    box-sizing: border-box;
    padding: .5em;
    margin: 0;
}

thead {
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
}
/*h3 {
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    font-weight: bold;
}*/
</style>




